import { By } from 'selenium-webdriver';
import { DriverLike, register } from '@oracle/oraclejet-webdriver';
import { ConveyorBeltWebElement } from './ConveyorBeltWebElement';
export { ConveyorBeltWebElement };

/**
 * Retrieve an instance of [ConveyorBeltWebElement](../classes/ConveyorBeltWebElement.html).
 * @example
 * ```javascript
 * import { findConveyorBelt } from '@oracle/oraclejet-core-pack/webdriver';
 * const el = await findConveyorBelt(driver, By.id('my-oj-c-conveyor-belt'));
 * ```
 * @param driver A WebDriver/WebElement instance from where the element will be
 * searched. If WebDriver is passed, the element will be searched globally in the
 * document. If WebElement is passed, the search will be relative to this element.
 * @param by The locator with which to find the element
 */
export async function findConveyorBelt(
  driver: DriverLike,
  by: By
): Promise<ConveyorBeltWebElement> {
  const webEl = await driver.findElement(by);
  // Check that the element is of type ConveyorBeltWebElement
  if (!(webEl instanceof ConveyorBeltWebElement)) {
    const tagName = await webEl.getTagName();
    let supplemental = '';
    if (webEl.constructor.name === 'ConveyorBeltWebElement') {
      supplemental = 'You likely have multiple installations of this package.';
    }
    throw Error(
      `findConveyorBelt(${by}) created ${webEl.constructor.name} for <${tagName}>, but expected ${ConveyorBeltWebElement.name}. ${supplemental}`
    );
  }
  return webEl;
}

register('oj-c-conveyor-belt', ConveyorBeltWebElement);
